<template>
    <div>
        <h1>{{ targetData.subject }}</h1>
        <h3>{{ targetData.sn }}</h3>
        <ul :class="[showAnswer? 'show-answer': '']">
            <li v-for="n in 4" :key="n">
                <div :class="[testRsult[n - 1] == targetData['c' + n]? 'is-true': 'is-false']">
                    <input type="checkbox" :id="'checkbox_'+ n" v-model="testRsult[n - 1]">
                    <label :for="'checkbox_'+ n">{{ targetData['a'+ n] }}</label>
                </div>
            </li>
        </ul>
        <div :class="[showAnswerOthers ? 'show-others-visible':'show-others-hidden']">{{targetData.others}}</div>
        <br>
        <button @click="doShowAnswer">對答案</button>
        <button @click="setRandom">下一題</button>
    </div>
</template>

<script>

    export default {
        data: function(){

            return {
                testList: [],
                random: null,
                testRsult: [false, false, false, false],
                showAnswer: false,
                showAnswerOthers: false,
            }
        },
        computed: {
            targetData: function(){
                let $result = {}
                
                let $testList = this.testList,
                    _random = this.random;
                    
                let $target = $testList[_random]

                if( $target ) {

                    let $key = ['sn', 'cate', 'subject', 'a1', 'a2', 'a3', 'a4', 'c1', 'c2', 'c3', 'c4','others']

                    $key.forEach((key, index) => {
                        $result[key] = $target[index]
                    });
                }

                return $result;
            }
        },
        methods: {
            setRandom: function(){
                this.resetTest();

                let $testList = this.testList,
                    _length = $testList.length;

                let _now = this.random,
                    _result = Math.floor(Math.random() * _length);

                if( _now !== _result ) {
                    this.$set(this, 'random', _result);
                }else {
                    this.setRandom();
                }
            },
            doShowAnswer: function(){
                this.$set(this, 'showAnswer', true);
                this.$set(this,'showAnswerOthers',true);
            },
            resetTest: function(){
                this.$set(this, 'showAnswer', false);
                this.$set(this,'showAnswerOthers',false);
                this.$set(this, 'testRsult', [false, false, false, false]);
            },
            initData: function(res){
                let $data = res.data;

                this.$set(this, 'testList', $data);

                this.setRandom();
            },
            getData: function(){
                let _url = 'https://script.google.com/macros/s/AKfycbxcFzZrlxNxOR6B5nTwVRs3SZZthzpus8L_W1CHLmqP9j6gu_NIE34gVcWdOQCDBResJA/exec';

                return this.$http.get(_url);
            },
            init: async function(){

                try {
                    let $response = await Promise.all([
                            this.getData(),
                        ]);

                    let [data] = $response;
                    
                    this.initData(data);

                }catch(error) {
                    console.errror(error)
                }
            }
        },
        created: function(){
            this.init();
        }
    }
</script>

<style scoped lang="scss">

    ul {
        padding: 0;
        margin: 0;
    }

    li {
        font-size: 22px;

        + li {
            margin-top: 10px;
        }
    }

    .show-answer  {

        div {
            border: 1px solid #000;
        }

        .is-true {
            border-color: green;
        }

        .is-false {
            border-color: red;
        }
    }

    
    .show-others-hidden {
        visibility:hidden;
    }
    
    .show-others-visible {
        visibility:visible;
    }
    
    button {
        font-size: 22px;
        margin-left: 16px;
    }
</style>
